import { useLoaderData, useNavigate } from "@remix-run/react";
import { useEffect } from "react";

export default function BannerProductPreviewRoute() {
    const data = useLoaderData();
    const navigate = useNavigate();

    // Close on ESC
    useEffect(() => {
        const handler = (e) => {
            if (e.key === "Escape") navigate(-1);
        };
        window.addEventListener("keydown", handler);
        return () => window.removeEventListener("keydown", handler);
    }, [navigate]);

    return (
        <div
            className="fixed inset-0 bg-black bg-opacity-40 flex items-center justify-center z-50"
            onClick={() => navigate(-1)} // close on outside click
        >
            <div
                className="w-[1080px] p-8 max-h-[80vh] overflow-y-auto svg-bg bg-[var(--banner-bg-color)]"
                onClick={(e) => e.stopPropagation()} // prevent close on inner click
            >
                {/* ✅ Data passed to modal */}
                <PreviewModal data={data} />
            </div>
        </div>
    );
}
